<template>
    <div id="edit-invoice">
        <h3>Edit Employee</h3>
        <div class="row">
            <form @submit.prevent="updateEmployee" class="col s12" style="margin: 70px">
                <div class="row">
                    <div class="input-field col s12">
                        <input  type="text" v-model="id" required >
                        
                              
  
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="billed_amount" required >
                        
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="date_issued" required >
                        
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="due_amount" required >
                        
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="due_date" required >
                        
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="msisdn" required >
                      
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="paid_amount" required >
                       
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="reference" required >
                        
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="recipient" required >
                       
                    </div>
                </div>
                <button type="submit" class="btn">Submit</button>
                <router-link to="/" class="btn grey">Cancel</router-link>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseinit'
export default {
    name: 'edit-invoice',
    data(){
        return {
            id : null,
            billed_amount: null,
            date_issued: null,
            due_amount: null,
            due_date: null,
            msisdn: null,
            paid_amount: null,
            reference: null,
            recipient: null
        }
    },
    beforeRouteEnter(to, from, next){
        db.firestore().collection('invoices').where('id',
         '==', to.params.id).get()
         .then(querySnapshot => {
             querySnapshot.forEach(doc => {
                 next(vm => {
                    var utcSeconds = doc.data().date_issued.seconds;
                    var d = new Date(0); 
                    d.setUTCSeconds(utcSeconds);
                    var issued_date = d.toLocaleDateString();

                    var utcSecond = doc.data().due_date.seconds;
                    var de = new Date(0); 
                    de.setUTCSeconds(utcSecond);
                    var date_due =  de.toLocaleDateString();


                    vm.id= doc.data().id,
                    vm.billed_amount= doc.data().billed_amount,
                    vm.date_issued= issued_date,
                    vm.due_amount= doc.data().due_amount,
                    vm.due_date= date_due,
                    vm.id= doc.data().id,
                    vm.msisdn= doc.data().msisdn,
                    vm.paid_amount= doc.data().paid_amount,
                    vm.reference= doc.data().reference,
                    vm.recipient= doc.data().recipient
                 })
             })
         })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData(){
            db.firestore().collection('invoices')
            .where('id', '==',
            this.$$route.params.id)
            .get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.id = doc.data().id,
                    this.billed_amount= doc.data().billed_amount,
                    this.date_issued= doc.data().date_issued,
                    this.due_amount= doc.data().due_amount,
                    this.due_date= doc.data().due_date,
                    this.id= doc.data().id,
                    this.msisdn= doc.data().msisdn,
                    this.paid_amount= doc.data().paid_amount,
                    this.reference= doc.data().reference,
                    this.recipient= doc.data().recipient
                })
            })

        },
        updateEmployee () {
            db.firestore().collection('invoices')
            .where('id', '==',
            this.$route.params.id)
            .get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    doc.ref.update({
                     
                        id : this.id,
                        billed_amount: this.billed_amount,
                        date_issued: this.date_issued,
                        due_amount: this.due_amount,
                        due_date: this.due_date,
                        id: this.id,
                        msisdn: this.msisdn,
                        paid_amount: this.paid_amount,
                        reference: this.reference,
                        recipient: this.recipient
                        
                    })
                    .then(() => {
                        this.$router.push({name: 'view-invoice',
                        params: {id: this.id}})
                    })
                })
            })
        }
    }
}
</script>


