<template>
    <div class="container">
        <div class="row">
            <div class="col-8" style=" margin: 0 auto; margin-top:50px; margin-bottom: 5-px;">
                 <form @submit.prevent="saveInvoice" class="col s12">
                        <div class="card" style="margin-bottom: 30px;">
                            <div class="card-body p-0">
                                <div >
                                    <h4 class="item" style=" border-bottom: none; margin-bottom: 40px;">{{send}}</h4>
                                </div>
                                <div class="input-field col s12">
                                    <input type="text" v-model="id" required >
                                    <label>Invoice ID#</label>
                                </div>
                            
                                <div class="input-field col s12">
                                    <input disabled type="text" v-model="date_issued" required >
                                    <label>Issued Date</label>
                                </div>
                            
                        
                                <div class="input-field col s12">
                                    <input  type="date" v-model="due_date" required >
                                    <label>Due Date</label>
                                </div>
                        
                        
                                <div class="input-field col s12">
                                    <input type="text" v-model="reference" required >
                                    <label>Reference</label>
                                </div>
                        
                        
                                <div class="input-field col s12">
                                    <input type="text" v-model="msisdn" required >
                                    <label>MSISDN</label>
                                </div>
                            
                        
                                <div class="input-field col s12">
                                    <input type="text" v-model="recipient" required>
                                    <label>Recipient</label>
                                </div>
                                
                            </div>
                        </div>




               
                                
                 

                 
                    <!-- <modal v-show="isModalVisible"  @close="closeModal"  @addItem="addItem"/> -->


<!---------------------------------------------------------------------------------------    End of item ---------------------------------------------------------->
<div >
        <button style="margin-top: 30px; margin-bottom: 5px ;   float: right;" type="text" class="btn"  id="modalBtn"  @click="displayModal"><i class="fa fa-plus"></i> Add Invoice Item</button>
</div>
                
<div class="main-content" id="mainTable">
 
  
  <table class="sTable">
    <tr>
      <th   v-bind:key='h' v-for="h in header">{{ h }}</th>
    </tr>

    <tr v-bind:key='s' v-for="s in student">
      <td>{{ s.item_name }}</td>
      <td>{{ s.item_description }}</td>
      <td>{{ s.item_unit_price }}</td>
    </tr>
  </table>
  <div id="modalAdd" class="modal">

    <div class="modalForm">
      <div class="modalHeader">
        <span class="closeBtn" @click="closeModal">&times;</span>
        <h3 class="mainHeader">Add Item</h3>

      </div>

      <div>
        <form class="forms" action="">
          Item name: <br>
          <input type="text" v-model="item_name"><br> 
          Item Description: <br>
          <input type="text" v-model="item_description"><br> 
          Item unit price: <br>
          <input type="text" v-model="item_unit_price"><br>


        </form>
      </div>
      <div class="modalFooter">
        <span class="addBtn" @click="addnew">Add Item</span>
      </div>
    </div>


  </div>

</div>





<!---------------------------------------------------------------------------------------    End of item ---------------------------------------------------------->
                <button style="margin-left: 10px; margin-top: 30px;" type="submit" class="btn">Submit</button>
                <router-link to="/" class="btn grey" style="margin-left: 10px; margin-top: 30px;">Cancel</router-link>

</form>


            </div>

        </div>
    </div>
    
</template>



<script>
import db from './firebaseinit'
// import Modal from './NavBars'
export default {
    name: 'new-invoice',
    data(){
        const timestamp = "";//firebase.firestore.FieldValue.serverTimestamp();

        return {
            billed_amount: null,
            date_issued: timestamp,
            due_amount: null,
            due_date: null,
            id: null,
            msisdn: null,
            paid_amount: null,
            reference: null,
            recipient: null,
            isModalVisible: false,
            header: ['ITEM', 'DESCRIPTION', 'UNIT PRICE'],
            student: [],
            item_name: '',
            item_description: '',
            item_unit_price: '',
            items: [],
            send: false,
        }
    },
    methods: {
        saveInvoice () {
            console.log(this.send)
            if(this.send){

                let amount = 0;
                for(var i = 0; i < this.student.length; i++ ){
                    console.log(this.student[i].item_name)
                    console.log(this.student[i].item_description)
                    console.log(this.student[i].item_unit_price)
                    this.items.push(this.student[i].item_name + ", " + this.student[i].item_description + ", " + this.student[i].item_unit_price)
                    amount = amount +  parseInt(this.student[i].item_unit_price);
                    console.log(" the amount :: ", parseInt(amount))
                }
                this.billed_amount = amount;
                this.due_amount = amount;
                var date = this.due_date;
                var date = this.due_date;
                console.log("--------------------------------------")
                console.log(date);
                db.firestore().collection('invoices').add({
                    billed_amount: this.billed_amount,
                    date_issued: this.date_issued,
                    due_amount: this.due_amount,
                    due_date: this.due_date,
                    id: this.id,
                    msisdn: this.msisdn,
                    paid_amount: this.paid_amount,
                    reference: this.reference,
                    recipient: this.recipient,
                    items: this.items,
                })
                .then( docRef => {
                    this.$router.push('/')
                })
                .catch(error => console.log(err))

            }
        },
        toggleModal(){
            this.$emit('toggleModal')
        },
        showModal() {
            this.isModalVisible = true;
        },
    //   closeModal() {
    //     this.isModalVisible = false;
    //     console.log(" the close item button has been clicked")
    //   },
      addItem(){
          console.log(" the save item button has been clicked")
      }, addnew() {
      this.student.push({
        'item_name': this.item_name,
        'item_description': this.item_description,
        'item_unit_price': this.item_unit_price,

      })
      console.log(this.student.length)
    
      
      console.log(this.items)
       this.send = true;
       console.log(this.send)
      this.closeModal()
    },
    displayModal() {
        this.send = false;
      document.getElementById('modalAdd').style.display = 'block';
    },
    closeModal() {
        if(this.student.length < 1){
            this.send = false;
        }
        
        console.log(this.send)
      document.getElementById('modalAdd').style.display = 'none';
     
    }
    }
}
</script>

<style>
/* Modal */

.modalbutton {
  background: #2e6cd1;
  padding: 10px 10px;
  color: white;
  border: 0;
  margin: 5px 2px;
}

.button {
  float: right;
  background: #2e6cd1;
  padding: 1em 2em;
  color: white;
  border: 0;
}

.button:hover {
  background: #333;
}

.modal {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: scroll;
  background-color: rgba(0, 0, 0, 0.5);
  animation-name: modalAni;
  animation-duration: 1s;
}

.modalForm {
  background: #f4f4f4;
  margin: 10% auto;
  width: 60%;
}

.modalHeader {
  width: 100%;
  background: coral;
  text-align: center;
  overflow: hidden;
}

.forms {
  width: 100%;
  overflow: hidden;
  padding: 0px 10px;
}

input[type=text] {
  width: 95%;
  padding: 12px 20px;
  margin: 8px 10px;
  box-sizing: border-box;
}

.closeBtn {
  background: transparent;
  font-size: 30px;
  float: right;
  width: 15%;
}

.closeBtn:hover,
.closeBtn:focus {
  color: #746c6c;
  cursor: pointer;
}

.modalFooter {
  background: coral;
  width: 100%;
  text-align: center;
  overflow: hidden;
  height: 50px;
}

.addBtn {
  font-size: 20px;
  text-align: center;
  padding: 10px;
}

.addBtn:hover,
.addBtn:focus,
.modalFooter:hover {
  background: rgb(172, 26, 26);
  color: #ffffff;
  cursor: pointer;
}


/*main content*/



table,
tr {
  width: 100%;
  border: 1px solid black;
  text-align: center;
  border-collapse: collapse;
  padding: 1px;
  background: #f4f4f4;
}

th {
  background: coral;
}

th,
td {
  border: 1px solid black;
  padding: 1px;
}

.mainHeader {
  padding: 10px 5px 0px 0px;
  margin: 0;
}

@keyframes modalAni {
  from {
    opacity: 0
  }
  to {
    opacity: 1
  }
}
</style>



