<template>
<div class="container">
    <div class="row">
        <div class="col-12">
            <div class="card"  id="invoice">
                <div class="card-body p-0">
                    <div class="row p-5">
                        <div class="col-md-6">
                           <img src="../assets/twilio_logo.svg" style="height: 200px; width: 200px; float:left;" >
                           <h1 style="float:left;  margin-left:20px; margin-bottom: 140px; color: rgb(242, 47, 70)" >Invoices</h1>
                        </div>

                        <div class="col-md-6 text-right">
                            <p class="font-weight-bold mb-1">Invoice {{reference}}</p>
                            <p>Due to: {{due_date}}</p>
                        </div>
                    </div>
                    

                    <!-- <hr class="my-5"> -->

                    <div class="row pb-5 p-5">
                        <div class="col-md-6">
                            <p class="font-weight-bold mb-4">Client Information</p>
                            <p class="mb-1">{{recipient}}</p>
                            <p class="mb-1">Buruburu, Nairobi</p>
                            <p class="mb-1">{{msisdn}}</p>
                        </div>

                        <div class="col-md-6 text-right">
                            <p class="font-weight-bold mb-4">Payment Details</p>
                            <p class="mb-1"><span>Billed Amount: </span> {{billed_amount}}</p>
                            <p class="mb-1"><span>Paid Amount: </span>  {{paid_amount}}</p>
                            <p class="mb-1"><span>Amount Due: </span>  {{due_amount}}</p>
                            <p class="mb-1"><span >Name: </span> {{recipient}}</p>
                        </div>
                    </div>

                    <div class="row p-5">
                        <div class="col-md-12">
                            


                    <div class="main-content" id="mainTable">
 
  
                        <table class="sTable">
                            <tr>
                            <th   v-bind:key='h' v-for="h in header">{{ h }}</th>
                            </tr>

                            <tr v-bind:key='s' v-for="s in student">
                            <td>{{ s[3] }}</td> <!-- ID ---->
                            <td>{{ s[0] }}</td> <!--- ITEM --->
                            <td>{{ s[1] }}</td> <!-- DESCRIPTION ---->
                            <td>1</td> <!--- QUANTITY --->
                            <td>{{ s[2]}}</td> <!--- UNIT COST --->
                            <td>{{ s[2]}}</td> <!--- TOTAL --->
                            </tr>
                        </table>
                        

                    </div>

                        <!------------------------------------------------   end of the table   -------------------------------------------->




                        </div>
                    </div>

                    <div class="d-flex flex-row-reverse bg-dark text-white p-4">
                        <div class="py-3 px-5 text-right">
                            <div class="mb-2">Billed Amount</div>
                            <div class="h2 font-weight-light">{{billed_amount}}</div>
                        </div>

                        <div class="py-3 px-5 text-right">
                            <div class="mb-2">Paid Amount</div>
                            <div class="h2 font-weight-light">{{paid_amount}}</div>
                        </div>

                        <div class="py-3 px-5 text-right">
                            <div class="mb-2">Due Amount</div>
                            <div class="h2 font-weight-light">{{due_amount}}</div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
       
    </div>
     <div  @click="pdfInvoice" class="fixed-action-btn  btn-floating btn-large #25D366">
          <i class="fa fa-whatsapp"></i>   
        </div>
    <!-- <div class="input-group"> -->
    <!-- <div class="input-group-prepend">
        <span class="input-group-text" id="inputGroupFileAddon01">Upload</span>
    </div> -->
    <!-- <div class="custom-file">
        <input type="file" class="custom-file-input" id="inputGroupFile01"  @change="processFile($event)"
        aria-describedby="inputGroupFileAddon01">
       
        <label class="custom-file-label" for="inputGroupFile01">Choose file</label>
    </div> -->
    </div>

        
<!-- </div> -->

</template>


<script>

import db from './firebaseinit'
import html2canvas from 'html2canvas'
import * as JsPDF from 'jspdf'
export default {
    name: 'invoice-item',
    data(){
        return {
            id: null,
            billed_amount: null,
            date_issued: null,
            due_amount: null,
            due_date: null,
            id: null,
            msisdn: null,
            paid_amount: null,
            reference: null,
            recipient: null,
            uploading : false,
            uploadEnd : false,
            downloadURL : '',
            progress: null,
            local_png_url: null,
            localpdf_url: null,
            student: null,
            header: ['ID #', 'ITEM', 'DESCRIPTION', 'QUANTITY', 'UNIT PRICE', 'TOTAL'],
            
        }
    },
    beforeRouteEnter(to, from, next){
        db.firestore().collection('invoices').where('id',
         '==', to.params.id).get()
         .then(querySnapshot => {
             querySnapshot.forEach(doc => {
                 next(vm => {
                    var utcSeconds = doc.data().date_issued.seconds;
                    var d = new Date(0); 
                    d.setUTCSeconds(utcSeconds);
                    var issued_date = d.toLocaleDateString();

                    var utcSecond = doc.data().due_date.seconds;
                    var de = new Date(0); 
                    de.setUTCSeconds(utcSecond);
                    var date_due =  de.toLocaleDateString();


                    var stdArray = []

                    var itemArray =  doc.data().items;
                    console.log("item array :: ", itemArray);
                    for( var i = 0; i < itemArray.length; i++){
                            console.log("individual item :: ", itemArray[i]);
                            
                            var newArray = itemArray[i].split(',');
                            var totalAmount = newArray[newArray.length - 1]
                            newArray.splice(0, 0, i);
                            newArray.splice(3, 0 , "1");
                            newArray.splice(4,0,totalAmount)
                            
                            console.log(" after the split :: " , newArray)
                            stdArray.push(newArray)
                    }
                    


                    vm.id= doc.data().id,
                    vm.billed_amount= doc.data().billed_amount,
                    vm.date_issued= issued_date,
                    vm.due_amount= doc.data().due_amount,
                    vm.due_date= date_due,
                    vm.id= doc.data().id,
                    vm.msisdn= doc.data().msisdn,
                    vm.paid_amount= doc.data().paid_amount,
                    vm.reference= doc.data().reference,
                    vm.recipient= doc.data().recipient,
                    vm.student = stdArray
                    
                    if( doc.data().paid_amount === null){
                        vm.paid_amount = 0;
                    }
                 })
             })
         })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        postInvoice(local_pdf_url, local_png_url){
            Vue.http.post('https://onekoechflask.herokuapp.com/invoice', {
            phone_number: this.msisdn,
            customer_name: this.recipient,
            invoice_amount: this.due_amount,
            invoice_number:this.reference,
            png_url: local_pdf_url,
            pdf_url: local_png_url
            
              
        })
        .then((res) => {
            console.log(res.body)
             alert("invoice sent to "  )

        })
        .catch((error)=> console.log(error))
    },pdfInvoice(){
            console.log("i am sending te invoice over")

            Vue.http.post('https://www.birichi.xyz/freepdf/tutorial/pdf_invoice.php', {
            id: this.id,
            billed_amount: this.billed_amount,
            date_issued: this.date_issued,
            due_amount: this.due_amount,
            due_date: this.due_date,
            msisdn: this.msisdn,
            paid_amount: this.paid_amount,
            reference: this.reference,
            recipient: this.recipient,
            student: this.student,
            header: ['ID #', 'ITEM', 'DESCRIPTION', 'QUANTITY', 'UNIT PRICE', 'TOTAL'],
            due_amount : this.due_amount
            
              
        })
        .then((res) => {
            console.log(res.body)
             alert("invoice sent to "  )

        })
        .catch((error)=> console.log(error))
    },
        fetchData(){
            db.firestore().collection('invoices')
            .where('id', '==',
            this.$route.params.id)
            .get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {

                    var stdArray = []

                    var itemArray =  doc.data().items;
                    console.log("item array :: ", itemArray);
                    for( var i = 0; i < itemArray.length; i++){
                            console.log("individual item :: ", itemArray[i]);
                            var newArray = itemArray[i].split(',');
                            console.log(" after the split :: " , newArray)
                            stdArray.push(newArray)
                    }
                this.id = doc.data().id,
                this.billed_amount= doc.data().billed_amount,
                this.date_issued= doc.data().date_issued,
                this.due_amount= doc.data().due_amount,
                this.due_date= doc.data().due_date,
                this.id= doc.data().id,
                this.msisdn= doc.data().msisdn,
                this.paid_amount= doc.data().paid_amount,
                this.reference= doc.data().reference,
                this.recipient= doc.data().recipient,
                this.student = stdArray
                })
            })

        },
        deleteEmployee(){
            if(confirm('Are you sure?')){
                db.firestore().collection('invoices').where('id', '==',
                this.$route.params.id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        doc.ref.delete();
                        this.$router.push('/')
                    })
                })
                
            }

        },
        print() {
		const filename  = 'reciept.pdf';

		html2canvas(document.querySelector('invoice')).then(canvas => {
			let pdf = new jsPDF('p', 'mm', 'a4');
			pdf.addImage(canvas.toDataURL('image/png'), 'PNG', 0, 0, 211, 298);
			pdf.save(filename);
		});
        },
        showCaptureRef() {
            console.log("this.$refs.capture: " + this.$refs.capture);
            let vc = this;
            return vc.$refs.capture;
        },
        downloadVisualReport () {
            let vc = this
            // alert("Descargando reporte visual")
            console.log('campaign-view#downloadVisualReport');
            window.html2canvas(vc.showCaptureRef()).then(canvas => {
                document.body.appendChild(canvas)
            }).catch((error) => {
                console.log("Erorr descargando reporte visual", error)
            });
        },
        download () {
            var quality = 4
            let invoice_number = this.reference
            html2canvas(document.querySelector('#invoice',{scale: quality}), {imageTimeout: 5000, useCORS: true}).then(canvas => {
                document.getElementById('invoice')
                let img = canvas.toDataURL('image/png')
                let pdf = new JsPDF('portrait', 'mm', 'a4')
                pdf.addImage(img, 'JPEG', 5, 5, 200, 287)
                // var file = pdf.save(this.reference + '.pdf')
                var blob = pdf.output('blob');
                console.log(blob)

                // Split the base64 string in data and contentType
                var block = img.split(";");

                // Get the content type of the image
                var contentType = block[0].split(":")[1];// In this case "image/png"
                // console.log(" the data contentType :: ", contentType )
                // get the real base64 content of the file
                var realData = block[1].split(",")[1];// In this case "R0lGODlhPQBEAPeoAJosM...."
                // console.log(" the realData  :: ", realData )
                // Convert it to a blob to upload
                var blob_img = this.b64toBlob(realData, contentType);
                console.log(blob_img)
                
                this.fileupload(blob, blob_img, invoice_number )
                //this.fileupload(blob_img)
               // this.postInvoice()
            });
        },
        fileupload(file, file_image, invoice_number){
            // Create a root reference
            console.log(" this is the invoice number ::" , invoice_number)
            var storageRef = db.storage().ref('invoices/'+invoice_number + '.pdf');
            let postInvoice = this.postInvoice

            var uploadTask = storageRef.put(file);

            uploadTask.on('state_changed', function(snapshot){

            var progressbar = (snapshot.bytesTransferred / snapshot.totalBytes) ;
            this.progress = progressbar
            console.log('Upload is ' + progressbar * 100 + '% done');
           if(progressbar === 1){
                
                
           }

            }, function(error) {
        
            console.log(error)
            }, 
            
            function() {

            uploadTask.snapshot.ref.getDownloadURL().then(function(downloadURL) {
                
                console.log('File local_pdf_url at', downloadURL);
                let local_pdf_url = downloadURL
                
                ////////////////////////////////////////////////////////////////////////////////////////////


                var storageRef = db.storage().ref('invoices/'+invoice_number + '.png');


                var uploadTask = storageRef.put(file_image);

                uploadTask.on('state_changed', function(snapshot){

                var progressbar = (snapshot.bytesTransferred / snapshot.totalBytes) ;
                this.progress = progressbar
                console.log('Upload is ' + progressbar * 100 + '% done');
            if(progressbar === 1){
                    
                    console.log(" the second file uploaded")
            }

                }, function(error) {
            
                console.log(error)
                }, 
                
                function() {

                uploadTask.snapshot.ref.getDownloadURL().then(function(downloadURL) {
                    
                    console.log('File file_image at', downloadURL);
                   
                    postInvoice(local_pdf_url , downloadURL )
                    
                    
                });
                });










                ////////////////////////////////////////////////////////////////////////////////////////////
                
            });
            });


        },
        processFile(event) {
            this.someData = event.target.files[0]
            this.fileupload(this.someData)
            console.log(this.someData);
        },
        selectFile () {
      this.$refs.uploadInput.click()
    },
    detectFiles (e) {
      let fileList = e.target.files || e.dataTransfer.files
      Array.from(Array(fileList.length).keys()).map(x => {
        this.upload(fileList[x])
      })
    },
    upload (file) {
      this.fileName = file.name
      this.uploading = true
      this.uploadTask = firestorage.ref('images/' + file.name).put(file)
    },
    deleteImage () {
      firestorage
        .ref('images/' + this.fileName)
        .delete()
        .then(() => {
          this.uploading = false
          this.uploadEnd = false
          this.downloadURL = ''
        })
        .catch((error) => {
          console.error(`file delete error occured: ${error}`)
        })
    }, 

    /**
 * Convert a base64 string in a Blob according to the data and contentType.
 * 
 * @param b64Data {String} Pure base64 string without contentType
 * @param contentType {String} the content type of the file i.e (image/jpeg - image/png - text/plain)
 * @param sliceSize {Int} SliceSize to process the byteCharacters
 * @see http://stackoverflow.com/questions/16245767/creating-a-blob-from-a-base64-string-in-javascript
 * @return Blob
 */
    b64toBlob(b64Data, contentType, sliceSize) {
        contentType = contentType || '';
        sliceSize = sliceSize || 512;

        var byteCharacters = atob(b64Data);
        var byteArrays = [];

        for (var offset = 0; offset < byteCharacters.length; offset += sliceSize) {
            var slice = byteCharacters.slice(offset, offset + sliceSize);

            var byteNumbers = new Array(slice.length);
            for (var i = 0; i < slice.length; i++) {
                byteNumbers[i] = slice.charCodeAt(i);
            }

            var byteArray = new Uint8Array(byteNumbers);

            byteArrays.push(byteArray);
        }

      var blob = new Blob(byteArrays, {type: contentType});
      return blob;
}

    
  }

       


    
}
</script>
<style>
body{
    color:black;
}
.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  display: table;
  transition: opacity .3s ease;
}

.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.modal-container {
  width: 300px;
  margin: 0px auto;
  padding: 20px 30px;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
  transition: all .3s ease;
  font-family: Helvetica, Arial, sans-serif;
}

.modal-header h3 {
  margin-top: 0;
  color: #42b983;
}

.modal-body {
  margin: 20px 0;
}

.modal-default-button {
  float: right;
}

/*
 * The following styles are auto-applied to elements with
 * transition="modal" when their visibility is toggled
 * by Vue.js.
 *
 * You can easily play with the modal transition by editing
 * these styles.
 */

.modal-enter {
  opacity: 0;
}

.modal-leave-active {
  opacity: 0;
}

.modal-enter .modal-container,
.modal-leave-active .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}

.progress-bar {
  margin: 10px 0;
}
input[type="file"] {
  position: absolute;
  clip: rect(0,0,0,0);
}
</style>



