<template>
    <div id="dashboard" style="margin: 30px">
        
        <div style="margin: 40px;">
            <img src="../assets/twilio_logo.svg" style="height: 200px; width: 200px; float:left;" >
            <h1 style="float:left;  margin-left:20px; margin-bottom: 140px; color: rgb(242, 47, 70)" >Invoices</h1>
        </div>
        <!-- <ul class="collection with-header">
            <li class="collection-header">
                <h4>Employees</h4> 
            </li>
            <li v-for="employee in employees" v-bind:key="employee.id "
             class="collection-item">
                <div class="chip" aria-colspan="3">{{employee.dept}}</div>
                {{employee.employee_id}}:{{employee.name}}

                <router-link class="secondary-content" v-bind:to="{name: 'view-employee',
                 params: {employee_id: employee.employee_id}}">
                 <i class="fa fa-eye"></i>
                 </router-link>
                 
            </li>
        </ul> -->
        <table id="firstTable" class="table table-bordered table-sm table-striped  margin: 10px; ">
            <thead>
                <tr>
                <th>#</th>
                <th>Issued Date</th>
                <th>Due Date</th>
                <th>Reference</th>
                <th>Recipient</th>
                <th>MSISDN</th>
                <th>Billed</th>
                <th>Due </th>
                <th>Paid</th>
                <th>Status</th>
                <th>Action</th>
                </tr>
            </thead>
            <tbody>
                <tr v-bind:key="invoice.id" v-for="invoice in invoices">
                    <td>{{invoice.int_id}}</td>
                    <td>{{invoice.date_issued}}</td>
                    <td>{{invoice.due_date}}</td>
                    <td>{{invoice.reference}}</td>
                    <td>{{invoice.recipient}}</td>
                    <td>{{invoice.msisdn}}</td>
                    <td>{{invoice.billed_amount}}</td>
                    <td>{{invoice.due_amount}}</td>
                    <td>{{invoice.paid_amount}}</td>
                    <td>{{invoice.status}}</td>
                    <td><router-link class="secondary-content" v-bind:to="{name: 'view-invoice',
                 params: {id: invoice.int_id}}">
                 <i class="fa fa-eye"></i>
                 </router-link></td>
                </tr>
            </tbody>
        </table>


        <div class="fixed-action-btn">
            
                <router-link to="/new-invoice"
                 class="btn-floating btn-large red">
                    <i class="fa fa-plus"></i>   
                </router-link>
                            
        </div>
    </div>
</template>
 
<script>
import db from './firebaseinit'
export default {
    name: 'dashboard',
    data(){
        return {
            invoices: []
            
        }
    },
    created(){
        db.firestore().collection('invoices').orderBy('id').get()
        .then(querysnapshot => {
            querysnapshot.forEach(doc => {
                console.log(doc.data());
                var utcSeconds = doc.data().date_issued.seconds;
                var d = new Date(0); 
                d.setUTCSeconds(utcSeconds);
                var issued_date = d.toLocaleDateString();

                var utcSecond = doc.data().due_date.seconds;
                var de = new Date(0); 
                de.setUTCSeconds(utcSecond);
                var date_due =  de.toLocaleDateString();
                const data = {
                    'id': doc.id,
                    'billed_amount': doc.data().billed_amount,
                    'date_issued': issued_date,
                    'due_amount': doc.data().due_amount,
                    'due_date': date_due,
                    'int_id': doc.data().id,
                    'msisdn': doc.data().msisdn,
                    'paid_amount': doc.data().paid_amount,
                    'reference': doc.data().reference,
                    'recipient': doc.data().recipient,
                    'status':  doc.data().status
                }
                this.invoices.push(data);
            })
        })
    }
}
