<template>
<div class="container">
    <div class="row">
        <div class="col-8" style=" margin: 0 auto; margin-top:50px;">
            <div class="card"  id="invoice">
                <div class="card-body p-0">
                    <div >
                        <h4 class="item" style=" border-bottom: none; margin-bottom: 40px;">{{reference}}</h4>
                    </div>
                    <div class="item">Invoice ID#:       <span style="margin-left:20px">  {{id}}  </span></div>
                    <div class="item">Issued Date:       <span style="margin-left:20px">   {{date_issued}} </span></div>
                    <div class="item">Due Date:          <span style="margin-left:20px">   {{due_date}} </span></div> 
                    <div class="item">Reference:         <span style="margin-left:20px">   {{reference}} </span></div>
                    <div class="item">MSISDN:            <span style="margin-left:20px">   {{msisdn}} </span></div> 
                    <div class="item">Billed Amount:     <span style="margin-left:20px">  {{billed_amount}} </span></div> 
                    <div class="item">Amount Due:        <span style="margin-left:20px">  {{due_amount}} </span></div>
                    <div class="item" style="margin-bottom: 30px;">Amount Paid:     <span style="margin-left:20px">     {{paid_amount}} </span></div>



                    <div class="main-content" id="mainTable">
 
  
                        <table class="sTable">
                            <tr>
                            <th   v-bind:key='h' v-for="h in header">{{ h }}</th>
                            </tr>

                            <tr v-bind:key='s' v-for="s in student">
                            <td>{{ s[0] }}</td>
                            <td>{{ s[1] }}</td>
                            <td>{{ s[2]}}</td>
                            </tr>
                        </table>
                        

                        </div>

                        <!------------------------------------------------   end of the table   -------------------------------------------->
                    
                </div>
            </div>
            <router-link to="/" class="btn grey">Back</router-link>
            <button @click="deleteEmployee" class="btn red">Delete</button>
        </div>

         
        <div class="fixed-action-btn" style="margin-bottom: 70px">
            
                <router-link v-bind:to="{name: 'invoice-item', params: {id: id} }" 
                 class="btn-floating btn-large black">
                    <i class="fa fa-print"></i>   
                </router-link>
                            
        </div>

        <div class="fixed-action-btn">
            
                <router-link v-bind:to="{name: 'edit-invoice', params: {id: id} }" 
                 class="btn-floating btn-large red">
                    <i class="fa fa-pencil"></i>   
                </router-link>
                            
        </div>
    </div>
</div>
    
</template>

<script>
import db from './firebaseinit'
export default {
    name: 'view-invoice',
    data(){
        return {
            id: null,
            billed_amount: null,
            date_issued: null,
            due_amount: null,
            due_date: null,
            id: null,
            msisdn: null,
            header: ['ITEM', 'DESCRIPTION', 'UNIT PRICE'],
            paid_amount: null,
            reference: null,
            recipient: null,
            student: null,
            
        }
    },
    beforeRouteEnter(to, from, next){
        db.firestore().collection('invoices').where('id',
         '==', to.params.id).get()
         .then(querySnapshot => {
             querySnapshot.forEach(doc => {
                 next(vm => {
                    var utcSeconds = doc.data().date_issued.seconds;
                    var d = new Date(0); 
                    d.setUTCSeconds(utcSeconds);
                    var issued_date = d.toLocaleDateString();

                    var utcSecond = doc.data().due_date.seconds;
                    var de = new Date(0); 
                    de.setUTCSeconds(utcSecond);
                    var date_due =  de.toLocaleDateString();
                    var stdArray = []

                    var itemArray =  doc.data().items;
                    console.log("item array :: ", itemArray);
                    for( var i = 0; i < itemArray.length; i++){
                            console.log("individual item :: ", itemArray[i]);
                            var newArray = itemArray[i].split(',');
                            console.log(" after the split :: " , newArray)
                            stdArray.push(newArray)
                    }
                    
                    
                   



                    vm.id= doc.data().id,
                    vm.billed_amount= doc.data().billed_amount,
                    vm.date_issued= issued_date,
                    vm.due_amount= doc.data().due_amount,
                    vm.due_date= date_due,
                    vm.id= doc.data().id,
                    vm.msisdn= doc.data().msisdn,
                    vm.paid_amount= doc.data().paid_amount,
                    vm.reference= doc.data().reference,
                    vm.recipient= doc.data().recipient,
                    vm.student = stdArray
                    
                     console.log(doc.data())
                 })
             })
         })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData(){
            db.firestore().collection('invoices')
            .where('id', '==',
            this.$route.params.id)
            .get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                     var stdArray = []

                    var itemArray =  doc.data().items;
                    console.log("item array :: ", itemArray);
                    for( var i = 0; i < itemArray.length; i++){
                            console.log("individual item :: ", itemArray[i]);
                            var newArray = itemArray[i].split(',');
                            console.log(" after the split :: " , newArray)
                            stdArray.push(newArray)
                    }
                this.id = doc.data().id,
                this.billed_amount= doc.data().billed_amount,
                this.date_issued= doc.data().date_issued,
                this.due_amount= doc.data().due_amount,
                this.due_date= doc.data().due_date,
                this.id= doc.data().id,
                this.msisdn= doc.data().msisdn,
                this.paid_amount= doc.data().paid_amount,
                this.reference= doc.data().reference,
                this.recipient= doc.data().recipient,
                this.student = stdArray
                })
            })

           

        },
        deleteEmployee(){
            if(confirm('Are you sure?')){
                db.firestore().collection('invoices').where('id', '==',
                this.$route.params.id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        doc.ref.delete();
                        this.$router.push('/')
                    })
                })
                
            }

        }
    }
}

</script>

<style>
.item{
    margin-top: 10px;
    margin-left: 30px;
    margin-right: 30px;
    border-bottom: 2px solid #ae2323;
}


/*main content*/



table,
tr {
  width: 95%;
  border: 1px solid black;
  text-align: center;
  border-collapse: collapse;
  padding: 1px;
  background: #f4f4f4;
}

th {
  background: coral;
}

th,
td {
  border: 1px solid black;
  padding: 1px;
}

.mainHeader {
  padding: 10px 5px 0px 0px;
  margin: 0;
}

@keyframes modalAni {
  from {
    opacity: 0
  }
  to {
    opacity: 1
  }
}
</style>



